<template>
  <div class="mw-page page storage" v-nav="{hideNavbar:true}">
    <div class="mw-page-header">
      <i class="left icon ion-ios-arrow-back" @click="$router.go(-1)"></i>
      <span>CNC</span>
      <i class="right"></i>
    </div>
    <div class="mw-page-content ">
      <cst-item label="部门名称" v-model="departInfo.dep_name" :action="action" ></cst-item>
      <cst-item label="部门主管" v-model="departInfo.dep_admin" action="check" :canEdit="false" :hasArrow="action==='action'" @click.native="selectAdmin"></cst-item>
      <cst-item label="上级部门" v-model="departInfo.parent_dep_name" :action="action" ></cst-item>
      <cst-item label="部门类型" v-model="departInfo.dep_type" action="check" :canEdit="false" :hasArrow="action==='action'" @click.native="selectDepType"></cst-item>
      <cst-item label="设备列表"  :action="'check'" :hasArrow="true" @click.native="$router.forward('productionDeptSub/equipmentList')"></cst-item>
      <cst-item label="设备点检分配"  :action="'check'" :hasArrow="true" v-model="num" @click.native="$router.forward('productionDeptSub/checklist')"></cst-item>
      <cst-item label="部门日历设置"  :action="'check'"  :hasArrow="true"></cst-item>
    </div>
    <time-picker v-on:chosen="chosen"
                 :showPicker="showPicker"
                 :pickerType="pickerType"
                 :pickerValue="pickerValue"
                 :pickerList='pickerList'
                 v-on:hideMask="showPicker=false">
    </time-picker>
  </div>
</template>

<script>
  import { pgDetail } from '@/api/production/equipment'
  import timePicker from "../../../components/timePicker"
  import CstItem from "../../../components/cstItem"
  export default{
    name:'equipment',
    components: {timePicker,CstItem},
    data(){
      return {
        action:'action',
        currSelect:'',//当前选择项
        showPicker:false,
        pickerType:'',
        pickerValue:'',
        pickerList:'',
        num:10,
        //页面传参
        dep_id:'',dep_name:'',upperDepName:'',
        depList:[
          {val:'黄杰',id:1},
          {val:'向辉',id:2},
          {val:'刘镜宏',id:3},
          {val:'林家纬',id:4},
          {val:'黄杰',id:5}
        ],
        depType:[
          {val:'人员',id:'1'},
          {val:'设备',id:'2'}
        ],
        departInfo:{//departInfo.dep_admin
          dep_name:'CNC',
          dep_admin:'黄杰',
          dep_admin_id:1,
          parent_dep_name:'伟博思科技有限公司生产部',
          dep_type:'人工',
          dep_type_id:'1'
        }
      }
    },
    computed:{

    },
    activated(){

    },
    mounted(){
      const param = this.$route.query;
      this.dep_id = param.dep_id;
      this.getPgDetail()
    },
    methods: {
      selectAdmin(){
        this.currSelect='Admin'//记住当前选择项
        this.showPicker=true
        this.pickerType='radio'
        this.pickerValue={
          val:this.departInfo.dep_admin,
          id:this.departInfo.dep_admin_id
        }
        this.pickerList=this.depList
      },
      selectDepType(){
        this.currSelect='DepType'
        this.showPicker=true
        this.pickerType='radio'
        this.pickerValue={
          val:this.departInfo.dep_type,
          id:this.departInfo.dep_type_id
        }
        this.pickerList=this.depType
      },
      chosen(e){
        if(this.currSelect==='Admin'){
          this.departInfo.dep_admin=e.val
          this.departInfo.dep_admin_id=e.id
          this.currSelect=''
        }else if(this.currSelect='DepType'){
          this.departInfo.dep_type=e.val
          this.departInfo.dep_type_id=e.id
          this.currSelect=''
        }

      },
      getPgDetail(){
        const  requestData={
          query:{
            pg_id:this.dep_id
          }
        }
        pgDetail(requestData).then(res => {
          }).catch(error => {
            $toast.show(error.description)
          })
      }
    }
  }
</script>

<style lang="scss" scoped>
</style>



